import { useEffect } from "react";
import { useHistory } from "react-router";
import { authTokenVar, isLoggedInVar } from "../components/apollo";
import { LOCALSTORAGE_TOKEN } from "../constants";

function getURLParams(url: any) {
  var result: any = {};
  url.replace(/[?&]{1}([^=&#]+)=([^&#]*)/g, function (s: any, k: any, v: any) {
    result[k] = decodeURIComponent(v);
  });
  return result;
}

export const GoogleLoginSuccess: any = () => {
  let history = useHistory();
  const tokenRawData = history.location.search;
  const token = getURLParams(tokenRawData).token;

  useEffect(() => {
    localStorage.setItem(LOCALSTORAGE_TOKEN, token);
    authTokenVar(token);
    isLoggedInVar(true);
    history.push("/");
  }, []);

  return <></>;
};
