import React from "react";
import { isMobile } from "react-device-detect";

interface IButtonProps {
  canClick: boolean;
  loading: boolean;
  actionText: string;
}

export const Button2: React.FC<IButtonProps> = ({
  canClick,
  loading,
  actionText,
}) => (
  <button
    className={`text-lg font-medium focus:outline-none text-white   px-4  transition-colors ${
      isMobile ? "py-2" : "py-3"
    } ${
      canClick
        ? "bg-blue-400 hover:bg-blue-500"
        : "bg-gray-300 pointer-events-none "
    }`}
  >
    {loading ? actionText : actionText}
  </button>
);
