import { gql } from "@apollo/client";

// 그래프큐엘 쿼리

export const ALLCHECKLISTS_QUERY = gql`
  query allCheckListsQuery {
    allCheckLists {
      error
      ok
      checkLists {
        id
        title
        checked
        profit
        difficulty
        urgency
      }
    }
  }
`;

export const CREATECHECKLIST_MUTATION = gql`
  mutation createCheckListMutation(
    $createCheckListInput: CreateCheckListInput!
  ) {
    createCheckList(input: $createCheckListInput) {
      ok
      error
    }
  }
`;

export const EDITCHECKLIST_MUTATION = gql`
  mutation editCheckListMutation($editCheckListInput: EditCheckListInput!) {
    editCheckList(input: $editCheckListInput) {
      ok
      error
    }
  }
`;

export const DELETECHECKLIST_MUTATION = gql`
  mutation deleteCheckListMutation(
    $deleteCheckListInput: DeleteCheckListInput!
  ) {
    deleteCheckList(input: $deleteCheckListInput) {
      ok
      error
    }
  }
`;
