import React from "react";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import { CreateAccount } from "../a5_pages/CreateAccount";
import { GoogleLoginSuccess } from "../a5_pages/GoogleLoginSuccess";
import { Login } from "../a5_pages/Login";

export function LoggedOutRouter() {
  return (
    <Router>
      <Switch>
        <Route path="/login/google/success">
          <GoogleLoginSuccess />
        </Route>
        <Route path="/create-account">
          <CreateAccount />
        </Route>
        <Route path="/">
          <Login />
        </Route>
      </Switch>
    </Router>
  );
}
