import { gql, useMutation } from "@apollo/client";
import { useForm } from "react-hook-form";
import { authTokenVar, isLoggedInVar } from "../components/apollo";
import Helmet from "react-helmet";

import {
  loginMutation,
  loginMutationVariables,
} from "/home/app/src/__generated__/loginMutation";
import { FormError } from "../a1_atoms/Form-error";
import { Link, useHistory } from "react-router-dom";
import { Button } from "../a1_atoms/Button";
import { LOCALSTORAGE_TOKEN } from "../constants";

interface LoginForm {
  email: string;
  password: string;
}

const LOGIN_MUTATION = gql`
  mutation loginMutation($loginInput: LoginInput!) {
    login(input: $loginInput) {
      ok
      error
      token
    }
  }
`;

export const Login = () => {
  // 유즈폼
  const {
    register,
    handleSubmit,
    getValues,
    formState,
    formState: { errors },
  } = useForm<LoginForm>({
    mode: "onChange",
  });

  let history = useHistory();

  const onCompleted = (data: loginMutation) => {
    const {
      login: { ok, token },
    } = data;
    if (ok && token) {
      localStorage.setItem(LOCALSTORAGE_TOKEN, token);
      authTokenVar(token);
      isLoggedInVar(true);
    }
  };

  const [loginMutation, { data: loginMutationResult, loading }] = useMutation<
    loginMutation,
    loginMutationVariables
  >(LOGIN_MUTATION, {
    onCompleted,
  });
  const onSubmit = () => {
    if (!loading) {
      const { email, password } = getValues();
      loginMutation({
        variables: {
          loginInput: {
            email,
            password,
          },
        },
      });
    }
  };
  return (
    <div className="flex items-center flex-col mt-10 lg:mt-28">
      <Helmet>
        <title>로그인 - 랭킹투두</title>
      </Helmet>
      <div className="w-full max-w-screen-sm flex flex-col px-5 items-center">
        <div className="text-4xl font-black mb-10 flex items-center">
          <span className="text-blue-500">Ranking</span>{" "}
          <span className="text-gray-900">Todo</span>
        </div>
        {/* <h4 className="w-full font-medium text-left text-3xl mb-5">
          환영합니다
        </h4> */}
        <form
          onSubmit={handleSubmit(onSubmit)}
          className="grid gap-3 mt-5 w-full mb-5"
        >
          <input
            {...register("email", {
              required: "이메일을 입력해주세요",
            })}
            required
            type="email"
            placeholder="Email"
            className="input"
          />
          {errors.email?.message && (
            <FormError errorMessage={errors.email?.message} />
          )}
          {errors.email?.type === "pattern" && (
            <FormError errorMessage={"Please enter a valid email"} />
          )}
          <input
            {...register("password", { required: "비밀번호를 입력해주세요" })}
            required
            type="password"
            placeholder="Password"
            className="input"
          />
          {errors.password?.message && (
            <FormError errorMessage={errors.password?.message} />
          )}
          <Button
            canClick={formState.isValid}
            loading={loading}
            actionText={"로그인"}
          />
          {loginMutationResult?.login.error && (
            <FormError errorMessage={loginMutationResult.login.error} />
          )}
        </form>

        <div className="flex my-3 mb-5">
          <a href="https://rank-todo-back.poketing.com:4000/auth/google">
            <div className="flex items-center border py-2 px-4 rounded-md shadow">
              <img
                src="https://developers.google.com/identity/images/g-logo.png?hl=ko"
                className="h-10"
              />
              <span className="ml-3">구글 계정으로 로그인</span>
            </div>
          </a>
        </div>

        <div className="">
          랭킹 투두가 처음이신가요?{" "}
          <div>
            <Link
              to="/create-account"
              className="text-blue-600 hover:underline"
            >
              회원가입 하러가기 →
            </Link>
          </div>
        </div>
      </div>
    </div>
  );
};
