import { gql, useMutation, useQuery } from "@apollo/client";
import React, { useEffect, useState } from "react";
import { useForm } from "react-hook-form";
import { Button2 } from "../../a1_atoms/Button2";
import { LOCALSTORAGE_TOKEN } from "/home/app/src/constants";
import { useMe } from "/home/app/src/hooks/useMe";
import { allCheckListsQuery } from "/home/app/src/__generated__/allCheckListsQuery";
import {
  createCheckListMutation,
  createCheckListMutationVariables,
} from "/home/app/src/__generated__/createCheckListMutation";
import { authTokenVar, isLoggedInVar } from "/home/app/src/components/apollo";

import {
  editCheckListMutation,
  editCheckListMutationVariables,
} from "/home/app/src/__generated__/editCheckListMutation";

import {
  deleteCheckListMutation,
  deleteCheckListMutationVariables,
} from "/home/app/src/__generated__/deleteCheckListMutation";

import { isMobile } from "react-device-detect";
import {
  ALLCHECKLISTS_QUERY,
  CREATECHECKLIST_MUTATION,
  DELETECHECKLIST_MUTATION,
  EDITCHECKLIST_MUTATION,
} from "../../query/CheckList";
import { useHistory } from "react-router-dom";

// 폼타입
type CreateTodoForm = {
  title: string;
  profit: number;
  difficulty: number;
  urgency: number;
};

export const TodoLIst = () => {
  // 리스트 쿼리 가져오기
  const { data: meData, loading: meLoading } = useMe();
  const {
    data: checkListData,
    loading: checkListLoading,
    refetch: ckecklistRefetch,
  } = useQuery<allCheckListsQuery>(ALLCHECKLISTS_QUERY);

  // 유즈폼
  const { register, handleSubmit, getValues, formState, reset } =
    useForm<CreateTodoForm>({
      mode: "onChange",
    });

  // 리스트 생성 뮤태이션
  const onCompleted = () => {
    ckecklistRefetch();
  };
  const [createCheckListMutation, { data: createCheckListResult }] =
    useMutation<createCheckListMutation, createCheckListMutationVariables>(
      CREATECHECKLIST_MUTATION,
      {
        onCompleted,
      }
    );
  const onSubmit = () => {
    const { title, profit, difficulty, urgency } = getValues();
    createCheckListMutation({
      variables: {
        createCheckListInput: {
          title,
          profit: +profit,
          difficulty: +difficulty,
          urgency: +urgency,
        },
      },
    });
    reset();
  };

  // 리스트 수정 뮤태이션
  const [editCheckListMutation, { data: editCheckListResult }] = useMutation<
    editCheckListMutation,
    editCheckListMutationVariables
  >(EDITCHECKLIST_MUTATION, {
    onCompleted,
  });

  const listCheckToggle = (checkListId: number, checked: boolean) => {
    if (checked) {
      editCheckListMutation({
        variables: {
          editCheckListInput: {
            checkListId,
            checked: false,
          },
        },
      });
    } else {
      editCheckListMutation({
        variables: {
          editCheckListInput: {
            checkListId,
            checked: true,
          },
        },
      });
    }
  };

  // 리스트 삭제 뮤태이션
  const [deleteCheckListMutation, { data: deleteCheckListResult }] =
    useMutation<deleteCheckListMutation, deleteCheckListMutationVariables>(
      DELETECHECKLIST_MUTATION,
      {
        onCompleted,
      }
    );

  // 정렬 로직
  const [newCheckListData, setNewCheckListData] = useState(
    checkListData?.allCheckLists.checkLists?.map((val) => ({
      ...val,
      value: 0,
    }))
  );

  const [tabData, setTabData] = useState([
    { title: "완료됨", isClicked: false },
    { title: "생성순", isClicked: true },
    { title: "예상이익순", isClicked: false },
    { title: "쉬움정도순", isClicked: false },
    { title: "긴급도순", isClicked: false },
    { title: "우선순위순", isClicked: false },
  ]);

  useEffect(() => {
    const newCheckList = checkListData?.allCheckLists.checkLists
      ?.slice(0, checkListData?.allCheckLists.checkLists?.length)
      .map((val) => ({
        ...val,
        value: val.profit + val.difficulty + val.urgency,
      }));

    const checkListSorted_complete = newCheckList?.filter((ele) => ele.checked);

    const checkListSorted_create = newCheckList?.filter((ele) => !ele.checked);

    const checkListSorted_profit = newCheckList
      ?.sort((a, b) => (a.profit > b.profit ? -1 : a.profit < b.profit ? 1 : 0))
      .filter((ele) => !ele.checked);
    const checkListSorted_difficulty = newCheckList
      ?.sort((a, b) =>
        a.difficulty > b.difficulty ? -1 : a.difficulty < b.difficulty ? 1 : 0
      )
      .filter((ele) => !ele.checked);

    const checkListSorted_urgency = newCheckList
      ?.sort((a, b) =>
        a.urgency > b.urgency ? -1 : a.urgency < b.urgency ? 1 : 0
      )
      .filter((ele) => !ele.checked);
    const checkListSorted_value = newCheckList
      ?.sort((a, b) => (a.value > b.value ? -1 : a.value < b.value ? 1 : 0))
      .filter((ele) => !ele.checked);

    const sortState = tabData
      .map((val) => {
        return val.isClicked;
      })
      .indexOf(true);

    if (sortState === 0) {
      setNewCheckListData(checkListSorted_complete);
    } else if (sortState === 1) {
      setNewCheckListData(checkListSorted_create);
    } else if (sortState === 2) {
      setNewCheckListData(checkListSorted_profit);
    } else if (sortState === 3) {
      setNewCheckListData(checkListSorted_difficulty);
    } else if (sortState === 4) {
      setNewCheckListData(checkListSorted_urgency);
    } else if (sortState === 5) {
      setNewCheckListData(checkListSorted_value);
    }
  }, [checkListData, checkListLoading, tabData]);

  const tabDataTogle = (idx: any) => {
    setTabData(
      tabData.map((listVal, listIdx) => {
        return listIdx === idx
          ? { ...listVal, isClicked: true }
          : { ...listVal, isClicked: false };
      })
    );
    setSortTitle(false);
  };

  const [sortTitle, setSortTitle] = useState(false);
  const sortTitleTogle = () => {
    setSortTitle(!sortTitle);
  };

  const [secondTotoList, setsecondTotoList] = useState(true);
  const secondTotoListTogle = () => {
    setsecondTotoList(!secondTotoList);
  };

  let history = useHistory();
  return isMobile ? (
    //
    // 모바일 뷰
    //
    <section>
      {/* 프로필 ㅁ */}
      <div className="w-11/12 mx-auto">
        <div className="flex flex-col items-end ">
          <h1>{meData?.me.email}</h1>
          <button
            onClick={() => {
              localStorage.removeItem(LOCALSTORAGE_TOKEN);
              authTokenVar("");
              isLoggedInVar(false);
              window.location.reload();
            }}
          >
            로그아웃
          </button>
        </div>
      </div>

      {/* 로고 ㅁ */}
      <div className="flex justify-center items-center  ">
        <div className="text-4xl font-black mt-4 mb-4">
          <span className="text-blue-500 ">Ranking</span>{" "}
          <span className="text-gray-900">Todo</span>
        </div>
      </div>

      {/* 입력창 ㅁ */}
      <div className="w-11/12 mx-auto">
        <div className="flex justify-center mt-12 w-full">
          <form
            onSubmit={handleSubmit(onSubmit)}
            className="flex flex-col gap-y-2 "
          >
            <input
              {...register("title", {
                required: "제목을 입력해야합니다 ",
              })}
              required
              placeholder="할일"
              className="input"
            />
            <input
              {...register("profit")}
              type="number"
              placeholder="예상이익"
              min="0"
              max="5"
              className="inputSmall"
            />
            <input
              {...register("difficulty")}
              type="number"
              placeholder="쉬움정도"
              min="0"
              max="5"
              className="inputSmall"
            />
            <input
              {...register("urgency")}
              type="number"
              placeholder="긴급도"
              min="0"
              max="5"
              className="inputSmall"
            />

            <Button2
              canClick={formState.isValid}
              loading={meLoading}
              actionText={"등록"}
            />
          </form>
        </div>
        <div>
          <div className="mx-auto flex justify-center mt-2 text-gray-400">
            랭킹 수치는 1부터 5까지 입력가능합니다.
          </div>
        </div>
      </div>

      {/* 정렬 버튼 ㅁ*/}
      <div className="w-11/12 mx-auto flex justify-end   mt-10">
        <div className="flex items-center mr-4 text-blue-600 font-bold">
          {tabData.filter((val) => val.isClicked === true)[0].title}
        </div>
        <div
          className="text-lg py-1 px-4 rounded-md border border-blue-200 text-gray-900 shadow cursor-pointer"
          onClick={() => {
            sortTitleTogle();
          }}
        >
          정렬 ↓
        </div>
      </div>
      {/* 리스트섹션 ㅁ */}
      {sortTitle && (
        <div className="w-11/12 mx-auto text-lg flex justify-end h-0 relative top-2">
          <div className=" w-max h-64 rounded-md border border-gray-200 text-gray-900 shadow-lg px-5 relative z-30 bg-white flex items-center">
            {/* 정렬제목 ㅁ */}
            <ul className="flex flex-col gap-x-2">
              {tabData.map((val, idx) => (
                <li
                  className={`cursor-pointer rounded px-2 py-1 transition duration-100 ${
                    val.isClicked ? "bg-blue-400 text-white font-bold" : ""
                  }`}
                  onClick={() => {
                    tabDataTogle(idx);
                  }}
                >
                  {val.title}
                </li>
              ))}
            </ul>
          </div>
        </div>
      )}
      {/* 리스트 ㅁ*/}
      <ul className="w-11/12 mx-auto flex flex-col items-center text-lg mt-6 mb-6 gap-4">
        {newCheckListData?.map(
          (val, idx) =>
            idx < 3 && (
              <li
                className="w-full flex flex-col  justify-center items-center"
                key={val.id}
              >
                <div className="w-full flex justify-center">
                  <div className="flex flex-col  border  rounded-md w-10/12 ">
                    <div className="flex w-full">
                      <div
                        className="cursor-pointer flex justify-start px-3"
                        onClick={() => {
                          listCheckToggle(val.id, Boolean(val.checked));
                        }}
                      >
                        {val.checked ? (
                          <i className="far fa-check-circle flex justify-center items-center text-2xl pt-px text-gray-700"></i>
                        ) : (
                          <i className="far fa-circle flex justify-center items-center text-2xl pt-px text-gray-700"></i>
                        )}
                      </div>
                      <div className="py-3">{val.title}</div>
                    </div>
                    <div className="flex flex-col items-end py-2 mr-3">
                      <div className=" flex justify-end font-bold text-blue-600 text-base">
                        가치: {val.value}
                      </div>
                      <div className="flex justify-end gap-x-3 text-sm text-gray-600 w-60">
                        <div>예상이익: {val.profit}</div>
                        <div>쉬움정도: {val.difficulty}</div>
                        <div>긴급도: {val.urgency}</div>
                      </div>
                    </div>
                  </div>
                  <div
                    className="flex items-center cursor-pointer"
                    onClick={() => {
                      deleteCheckListMutation({
                        variables: {
                          deleteCheckListInput: {
                            checkListId: val.id,
                          },
                        },
                      });
                    }}
                  >
                    <i className="far fa-trash-alt flex justify-center items-center text-xl pt-px text-gray-700 pl-3"></i>
                  </div>
                </div>
              </li>
            )
        )}
      </ul>

      {/* 접힌 리스트 접기 버튼 */}
      <div className="w-11/12 mx-auto flex justify-center mb-6">
        {newCheckListData?.length && newCheckListData?.length > 3 && (
          <div
            className="flex justify-center items-center text-lg border rounded-lg p-1 w-36  border-blue-200 text-gray-900 shadow cursor-pointer"
            onClick={() => {
              secondTotoListTogle();
            }}
          >
            <>{secondTotoList ? <>접기 ↑</> : <>펼치기 ↓</>}</>
          </div>
        )}
      </div>

      {/* 접힌 리스트 */}
      {secondTotoList && (
        <ul className="w-11/12 mx-auto flex flex-col items-center text-lg mt-6 mb-6 gap-4">
          {newCheckListData?.map(
            (val, idx) =>
              idx >= 3 && (
                <li
                  className="w-full flex flex-col  justify-center items-center"
                  key={val.id}
                >
                  <div className="w-full flex justify-center">
                    <div className="flex flex-col  border  rounded-md w-10/12 ">
                      <div className="flex w-full">
                        <div
                          className="cursor-pointer flex justify-start px-3"
                          onClick={() => {
                            listCheckToggle(val.id, Boolean(val.checked));
                          }}
                        >
                          {val.checked ? (
                            <i className="far fa-check-circle flex justify-center items-center text-2xl pt-px text-gray-700"></i>
                          ) : (
                            <i className="far fa-circle flex justify-center items-center text-2xl pt-px text-gray-700"></i>
                          )}
                        </div>
                        <div className="py-3">{val.title}</div>
                      </div>
                      <div className="flex flex-col items-end py-2 mr-3">
                        <div className=" flex justify-end font-bold text-blue-600 text-base">
                          가치: {val.value}
                        </div>
                        <div className="flex justify-end gap-x-3 text-sm text-gray-600 w-60">
                          <div>예상이익: {val.profit}</div>
                          <div>쉬움정도: {val.difficulty}</div>
                          <div>긴급도: {val.urgency}</div>
                        </div>
                      </div>
                    </div>
                    <div
                      className="flex items-center cursor-pointer"
                      onClick={() => {
                        deleteCheckListMutation({
                          variables: {
                            deleteCheckListInput: {
                              checkListId: val.id,
                            },
                          },
                        });
                      }}
                    >
                      <i className="far fa-trash-alt flex justify-center items-center text-xl pt-px text-gray-700 pl-3"></i>
                    </div>
                  </div>
                </li>
              )
          )}
        </ul>
      )}
    </section>
  ) : (
    //
    // 피씨뷰
    //
    <section>
      <div className="mt-2 mb-10 flex gap-4 justify-end mr-6 text-gray-900">
        <h1>{meData?.me.email}</h1>
        <button
          onClick={() => {
            localStorage.removeItem(LOCALSTORAGE_TOKEN);
            authTokenVar("");
            isLoggedInVar(false);
            window.location.reload();
          }}
        >
          로그아웃
        </button>
      </div>
      {/* 로고 */}
      <div className="flex justify-center items-center">
        <div className="text-5xl font-black mb-4">
          <span className="text-blue-500">Ranking</span>{" "}
          <span className="text-gray-900">Todo</span>
        </div>
      </div>
      {/* 입력창 */}
      <div>
        <div className="flex justify-center mt-12">
          <form onSubmit={handleSubmit(onSubmit)} className="flex gap-x-2 ">
            <input
              {...register("title", {
                required: "제목을 입력해야합니다 ",
              })}
              required
              placeholder="할일"
              className="input w-96"
            />
            <input
              {...register("profit")}
              type="number"
              placeholder="예상이익"
              min="0"
              max="5"
              className="inputSmall w-24"
            />
            <input
              {...register("difficulty")}
              type="number"
              placeholder="쉬움정도"
              min="0"
              max="5"
              className="inputSmall w-24"
            />
            <input
              {...register("urgency")}
              type="number"
              placeholder="긴급도"
              min="0"
              max="5"
              className="inputSmall w-24"
            />

            <Button2
              canClick={formState.isValid}
              loading={meLoading}
              actionText={"등록"}
            />
          </form>
        </div>
        <div>
          <div className=" max-w-screen-md mx-auto flex justify-end mt-2 text-gray-400">
            랭킹 수치는 1부터 5까지 입력가능합니다.
          </div>
        </div>
      </div>
      {/* 정렬 버튼 */}
      <div className=" max-w-screen-md mx-auto flex justify-end   mt-10">
        <div className="flex items-center mr-4 text-blue-600 font-bold">
          {tabData.filter((val) => val.isClicked === true)[0].title}
        </div>
        <div
          className="text-lg py-1 px-4 rounded-md border border-blue-200 text-gray-900 shadow cursor-pointer"
          onClick={() => {
            sortTitleTogle();
          }}
        >
          정렬 ↓
        </div>
      </div>
      {/* 리스트섹션  */}
      {sortTitle && (
        <div className=" max-w-screen-md mx-auto text-lg flex justify-end h-0 relative top-2">
          <div className=" w-max h-16 rounded-md border border-gray-200 text-gray-900 shadow-lg px-5 relative z-30 bg-white flex items-center">
            {/* 정렬제목 */}
            <ul className="flex justify-end gap-x-2">
              {tabData.map((val, idx) => (
                <li
                  className={`cursor-pointer rounded px-2 py-1 transition duration-100 ${
                    val.isClicked ? "bg-blue-400 text-white font-bold" : ""
                  }`}
                  onClick={() => {
                    tabDataTogle(idx);
                  }}
                >
                  {val.title}
                </li>
              ))}
            </ul>
          </div>
        </div>
      )}
      {/* 리스트 */}
      <ul className="flex flex-col items-center text-lg mt-6 mb-6 gap-4">
        {newCheckListData?.map(
          (val, idx) =>
            idx < 3 && (
              <li
                className="flex justify-center"
                key={val.id}
                style={{ width: "50rem" }}
              >
                <div className="flex w-full">
                  <div className="flex gap-5 border px-4  rounded-md ">
                    <div
                      className="w-10 cursor-pointer flex justify-center"
                      onClick={() => {
                        listCheckToggle(val.id, Boolean(val.checked));
                      }}
                    >
                      {" "}
                      {val.checked ? (
                        <i className="far fa-check-circle flex justify-center items-center text-2xl pt-px text-gray-700"></i>
                      ) : (
                        <i className="far fa-circle flex justify-center items-center text-2xl pt-px text-gray-700"></i>
                      )}
                    </div>
                    <div className="w-80 mr-20  py-3 flex items-center ">
                      {" "}
                      {val.title}
                    </div>
                    <div className="flex flex-col items-end py-2 mr-3">
                      <div className="w-24 flex justify-end font-bold text-blue-600 text-base">
                        가치: {val.value}
                      </div>
                      <div className="flex justify-end gap-x-3 text-sm text-gray-600 w-60">
                        <div>예상이익: {val.profit}</div>
                        <div>쉬움정도: {val.difficulty}</div>
                        <div>긴급도: {val.urgency}</div>
                      </div>
                    </div>
                  </div>
                  <div
                    className="flex juce items-center cursor-pointer"
                    onClick={() => {
                      deleteCheckListMutation({
                        variables: {
                          deleteCheckListInput: {
                            checkListId: val.id,
                          },
                        },
                      });
                    }}
                  >
                    <i className="far fa-trash-alt flex justify-center items-center text-xl pt-px text-gray-700 px-3"></i>
                  </div>
                </div>
              </li>
            )
        )}
      </ul>

      {/* 접힌 리스트 */}
      <div className="max-w-screen-md mx-auto flex justify-center">
        {newCheckListData?.length && newCheckListData?.length > 3 ? (
          <div
            className="flex justify-center items-center text-lg border rounded-lg p-1 w-36  border-blue-200 text-gray-900 shadow cursor-pointer"
            onClick={() => {
              secondTotoListTogle();
            }}
          >
            <>{secondTotoList ? <>접기 ↑</> : <>펼치기 ↓</>}</>
          </div>
        ) : (
          <></>
        )}
      </div>

      {/* 접힌 리스트 */}
      {secondTotoList && (
        <ul className="flex flex-col items-center text-lg mt-6 mb-10 gap-4">
          {newCheckListData?.map(
            (val, idx) =>
              idx >= 3 && (
                <li
                  className="flex justify-center"
                  key={val.id}
                  style={{ width: "50rem" }}
                >
                  <div className="flex w-full">
                    <div className="flex gap-5 border px-4  rounded-md ">
                      <div
                        className="w-10 cursor-pointer flex justify-center"
                        onClick={() => {
                          listCheckToggle(val.id, Boolean(val.checked));
                        }}
                      >
                        {" "}
                        {val.checked ? (
                          <i className="far fa-check-circle flex justify-center items-center text-2xl pt-px text-gray-700"></i>
                        ) : (
                          <i className="far fa-circle flex justify-center items-center text-2xl pt-px text-gray-700"></i>
                        )}
                      </div>
                      <div className="w-80 mr-20  py-3 flex items-center ">
                        {" "}
                        {val.title}
                      </div>
                      <div className="flex flex-col items-end py-2 mr-3">
                        <div className="w-24 flex justify-end font-bold text-blue-600 text-base">
                          가치: {val.value}
                        </div>
                        <div className="flex justify-end gap-x-3 text-sm text-gray-600 w-60">
                          <div>예상이익: {val.profit}</div>
                          <div>쉬움정도: {val.difficulty}</div>
                          <div>긴급도: {val.urgency}</div>
                        </div>
                      </div>
                    </div>
                    <div
                      className="flex juce items-center cursor-pointer"
                      onClick={() => {
                        deleteCheckListMutation({
                          variables: {
                            deleteCheckListInput: {
                              checkListId: val.id,
                            },
                          },
                        });
                      }}
                    >
                      <i className="far fa-trash-alt flex justify-center items-center text-xl pt-px text-gray-700 px-3"></i>
                    </div>
                  </div>
                </li>
              )
          )}
        </ul>
      )}
    </section>
  );
};
